/* Product Details Page Styles */

/* Loading State */
.product-details-loading {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 50vh;
  font-size: 18px;
  color: #666;
}

.spinner {
  width: 50px;
  height: 50px;
  border: 3px solid #f3f3f3;
  border-top: 3px solid #007bff;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 20px;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/* Error State */
.product-details-error {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 50vh;
  text-align: center;
  padding: 20px;
}

.error-title {
  font-size: 24px;
  color: #dc3545;
  margin-bottom: 10px;
}

.error-message {
  font-size: 16px;
  color: #666;
  margin-bottom: 20px;
}

.back-button {
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  text-decoration: none;
  display: inline-block;
  transition: background-color 0.2s ease;
}

.back-button:hover {
  background-color: #0056b3;
}

/* Main Container */
.product-details-container {
  min-height: 100vh;
  background-color: #f8f9fa;
}

.product-details-wrapper {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

/* Product Header */
.product-header {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  padding: 40px 0;
  margin-bottom: 40px;
}

.product-header-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  display: flex;
  align-items: center;
  gap: 20px;
}

.product-header h1 {
  font-size: 2.5rem;
  margin: 0;
  font-weight: 700;
}

/* Breadcrumb */
.breadcrumb {
  background-color: white;
  padding: 15px 0;
  border-bottom: 1px solid #eee;
  margin-bottom: 0;
}

.breadcrumb-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 14px;
  color: #666;
}

.breadcrumb-link {
  color: #007bff;
  text-decoration: none;
  transition: color 0.2s ease;
}

.breadcrumb-link:hover {
  color: #0056b3;
}

.breadcrumb-separator {
  color: #ccc;
}

/* Product Content */
.product-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 60px;
  margin-bottom: 60px;
}

/* Product Image */
.product-image-container {
  position: sticky;
  top: 100px;
}

.product-image {
  width: 100%;
  height: 400px;
  object-fit: cover;
  border-radius: 12px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.product-image:hover {
  transform: scale(1.02);
}

/* Product Info */
.product-info {
  padding: 20px 0;
}

.product-title {
  font-size: 2.5rem;
  font-weight: 700;
  color: #333;
  margin-bottom: 15px;
  line-height: 1.2;
}

.product-price {
  font-size: 2rem;
  font-weight: 700;
  color: #28a745;
  margin-bottom: 20px;
}

.product-description {
  font-size: 1.1rem;
  line-height: 1.6;
  color: #666;
  margin-bottom: 30px;
}

/* Product Features */
.product-features {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  margin-bottom: 30px;
}

.feature-item {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 15px;
  background-color: #f8f9fa;
  border-radius: 8px;
  border-left: 4px solid #007bff;
}

.feature-icon {
  color: #007bff;
  font-size: 1.2rem;
}

.feature-text {
  font-weight: 500;
  color: #333;
}

/* Quantity and Actions */
.quantity-section {
  margin-bottom: 30px;
}

.quantity-label {
  display: block;
  font-weight: 600;
  color: #333;
  margin-bottom: 10px;
}

.quantity-controls {
  display: flex;
  align-items: center;
  gap: 15px;
}

.quantity-input {
  width: 80px;
  padding: 10px;
  border: 2px solid #ddd;
  border-radius: 5px;
  text-align: center;
  font-size: 16px;
  font-weight: 600;
}

.quantity-input:focus {
  outline: none;
  border-color: #007bff;
}

.action-buttons {
  display: flex;
  gap: 15px;
  flex-wrap: wrap;
}

.action-btn {
  padding: 15px 30px;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  gap: 8px;
  text-decoration: none;
}

.add-to-cart-btn {
  background-color: #28a745;
  color: white;
  flex: 1;
  justify-content: center;
}

.add-to-cart-btn:hover {
  background-color: #218838;
  transform: translateY(-2px);
}

.wishlist-btn {
  background-color: #fff;
  color: #dc3545;
  border: 2px solid #dc3545;
  min-width: 50px;
  justify-content: center;
}

.wishlist-btn:hover {
  background-color: #dc3545;
  color: white;
}

.share-btn {
  background-color: #fff;
  color: #007bff;
  border: 2px solid #007bff;
  min-width: 50px;
  justify-content: center;
}

.share-btn:hover {
  background-color: #007bff;
  color: white;
}

/* Product Specifications */
.product-specs {
  background-color: white;
  padding: 30px;
  border-radius: 12px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.08);
  margin-bottom: 40px;
}

.specs-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: #333;
  margin-bottom: 20px;
  padding-bottom: 10px;
  border-bottom: 2px solid #f0f0f0;
}

.specs-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 15px;
}

.spec-item {
  display: flex;
  justify-content: space-between;
  padding: 10px 0;
  border-bottom: 1px solid #f0f0f0;
}

.spec-label {
  font-weight: 600;
  color: #666;
}

.spec-value {
  color: #333;
}

/* Related Products */
.related-products {
  background-color: white;
  padding: 40px;
  border-radius: 12px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.08);
}

.related-title {
  font-size: 1.8rem;
  font-weight: 700;
  color: #333;
  margin-bottom: 30px;
  text-align: center;
}

.related-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 30px;
}

.related-item {
  text-align: center;
  padding: 20px;
  border-radius: 8px;
  transition: transform 0.2s ease;
  cursor: pointer;
}

.related-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
}

.related-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 15px;
}

.related-name {
  font-size: 1.1rem;
  font-weight: 600;
  color: #333;
  margin-bottom: 10px;
}

.related-price {
  font-size: 1.2rem;
  font-weight: 700;
  color: #28a745;
}

/* Header Navigation */
.product-header-nav {
  top: 0;
  padding-top: 20px;
  margin-left: -190px;
}

.header-nav-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.back-button-nav {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border: none;
  color: white;
  cursor: pointer;
  font-size: 1em;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 12px 24px;
  border-radius: 12px;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.2);
  text-transform: uppercase;
  letter-spacing: 0.5px;
  position: relative;
  overflow: hidden;
}

.back-button-nav:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(102, 126, 234, 0.4);
}

/* Main Product Container */
.product-main-container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 60px 20px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 80px;
  align-items: start;
  background-color: #f8f8f8;
}

/* Product Images */
.product-images-column {
  position: sticky;
  top: 100px;
}

.main-image-container {
  width: 100%;
  height: 550px;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.1);
  margin-bottom: 25px;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #f0f0f0;
  position: relative;
  transition: all 0.3s ease;
}

.main-image-container::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.main-image-container:hover {
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.15);
  transform: translateY(-2px);
}

.main-image-container:hover::before {
  opacity: 1;
}

.main-product-image {
  width: 100%;
  height: 100%;
  object-fit: contain;
  padding: 20px;
}

/* Product Information */
.product-info-column {
  padding: 30px;
  background: white;
  border-radius: 20px;
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.08);
  border: 1px solid #f0f0f0;
  position: relative;
}

.product-header-section {
  margin-bottom: 30px;
}

.product-meta {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 15px;
}

.category-badge {
  background-color: #e9f7ff;
  color: #007bff;
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 0.9em;
  font-weight: 500;
}

.rating-stars {
  display: flex;
  align-items: center;
  gap: 5px;
  color: #ffc107;
}

.rating-text {
  color: #666;
  margin-left: 8px;
}

.product-title-main {
  font-size: 2.8em;
  font-weight: 700;
  color: #2d3748;
  margin-bottom: 20px;
  line-height: 1.2;
  background: linear-gradient(135deg, #2d3748 0%, #4a5568 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.product-price-main {
  font-size: 2.5em;
  font-weight: 800;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 25px;
  position: relative;
}

.product-price-main::after {
  content: "";
  position: absolute;
  bottom: -8px;
  left: 0;
  width: 60px;
  height: 3px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 2px;
}

/* Stock Status */
.stock-status-container {
  border-radius: 8px;
  padding: 15px;
  margin-bottom: 25px;
  display: flex;
  align-items: center;
  gap: 10px;
}

.stock-status-in-stock {
  background-color: #d4edda;
  border: 1px solid #c3e6cb;
}

.stock-status-out-of-stock {
  background-color: #f8d7da;
  border: 1px solid #f5c6cb;
}

.stock-status-invalid {
  background-color: #fff3cd;
  border: 1px solid #ffeaa7;
}

.stock-indicator-dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
}

.stock-indicator-in-stock {
  background-color: #28a745;
}

.stock-indicator-out-of-stock {
  background-color: #dc3545;
}

.stock-indicator-invalid {
  background-color: #ff6b35;
}

.stock-status-text {
  font-weight: 600;
}

.stock-text-in-stock {
  color: #155724;
}

.stock-text-out-of-stock {
  color: #721c24;
}

.stock-text-invalid {
  color: #856404;
}

/* Stock Status in Specs */
.spec-value.stock-in-stock {
  color: #28a745;
}

.spec-value.stock-out-of-stock {
  color: #dc3545;
}

.spec-value.stock-invalid {
  color: #ff6b35;
}

/* Product Description */
.description-section {
  margin-bottom: 30px;
}

.description-title {
  font-size: 1.3em;
  font-weight: 600;
  margin-bottom: 15px;
  color: #333;
}

.description-text {
  color: #666;
  line-height: 1.6;
  font-size: 1.1em;
}

/* Quantity Selector */
.quantity-section {
  margin-bottom: 30px;
}

.quantity-title {
  font-size: 1.3em;
  font-weight: 600;
  margin-bottom: 15px;
  color: #333;
}

.quantity-controls-container {
  display: flex;
  align-items: center;
  gap: 15px;
  background-color: #f8f9fa;
  padding: 15px;
  border-radius: 8px;
  border: 1px solid #e9ecef;
}

.quantity-button {
  width: 40px;
  height: 40px;
  font-size: 1.2em;
  border-radius: 8px;
  border: 1px solid #dee2e6;
  background: white;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
}

.quantity-button:hover {
  background-color: #e9ecef;
}

.quantity-display {
  min-width: 50px;
  text-align: center;
  font-size: 1.2em;
  font-weight: 600;
  color: #333;
}

.quantity-available {
  color: #666;
  font-size: 0.9em;
}

/* Action Buttons */
.action-buttons-section {
  margin-bottom: 30px;
}

.action-buttons-row {
  display: flex;
  gap: 15px;
  margin-bottom: 15px;
}

.add-to-cart-button {
  flex: 1;
  padding: 20px 0;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  border-radius: 15px;
  font-weight: 700;
  font-size: 1.2em;
  cursor: pointer;
  opacity: 1;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
  position: relative;
  overflow: hidden;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.add-to-cart-button::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #764ba2 0%, #667eea 100%);
  transition: left 0.3s ease;
  z-index: 0;
}

.add-to-cart-button:hover:not(:disabled)::before {
  left: 0;
}

.add-to-cart-button:hover:not(:disabled) {
  transform: translateY(-3px);
  box-shadow: 0 12px 30px rgba(102, 126, 234, 0.4);
}

.add-to-cart-button span {
  position: relative;
  z-index: 1;
}

.add-to-cart-button:disabled {
  background: #6c757d;
  cursor: not-allowed;
  opacity: 0.6;
  transform: none;
  box-shadow: none;
}

.add-to-cart-button:disabled::before {
  display: none;
}

.wishlist-button {
  width: 60px;
  padding: 18px 0;
  background: #f8f9fa;
  color: #666;
  border: 1px solid #dee2e6;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
}

.wishlist-button.active {
  background: #dc3545;
  color: white;
}

.wishlist-button:hover {
  background: #e9ecef;
}

.wishlist-button.active:hover {
  background: #c82333;
}

.share-button {
  width: 60px;
  padding: 18px 0;
  background: #f8f9fa;
  color: #666;
  border: 1px solid #dee2e6;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
}

.share-button:hover {
  background: #e9ecef;
}

.cart-message {
  padding: 12px 16px;
  border-radius: 8px;
  text-align: center;
  font-weight: 500;
}

.cart-message.success {
  background-color: #d4edda;
  color: #155724;
  border: 1px solid #c3e6cb;
}

.cart-message.error {
  background-color: #f8d7da;
  color: #721c24;
  border: 1px solid #f5c6cb;
}

/* Product Features */
.product-features-section {
  background-color: white;
  border-radius: 12px;
  padding: 25px;
  border: 1px solid #e9ecef;
  margin-bottom: 30px;
}

.features-title {
  font-size: 1.3em;
  font-weight: 600;
  margin-bottom: 20px;
  color: #333;
}

.features-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}

.feature-item {
  display: flex;
  align-items: center;
  gap: 12px;
}

.feature-icon {
  font-size: 1.2em;
}

.feature-icon-truck {
  color: #28a745;
}

.feature-icon-shield {
  color: #007bff;
}

.feature-icon-undo {
  color: #ffc107;
}

.feature-icon-eye {
  color: #6f42c1;
}

.feature-text {
  color: #666;
}

/* Product Specifications */
.specifications-section {
  background-color: white;
  border-radius: 12px;
  padding: 25px;
  border: 1px solid #e9ecef;
}

.specifications-title {
  font-size: 1.3em;
  font-weight: 600;
  margin-bottom: 20px;
  color: #333;
}

.specifications-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 15px;
}

.spec-item {
  display: flex;
  flex-direction: column;
}

.spec-label {
  color: #666;
  font-size: 0.9em;
}

.spec-value {
  color: #333;
  font-weight: 500;
}

/* Responsive Design */
@media (max-width: 768px) {
  .product-header h1 {
    font-size: 2rem;
  }

  .product-content {
    grid-template-columns: 1fr;
    gap: 30px;
  }

  .product-image-container {
    position: static;
  }

  .product-image {
    height: 300px;
  }

  .product-title {
    font-size: 2rem;
  }

  .product-price {
    font-size: 1.5rem;
  }

  .action-buttons {
    flex-direction: column;
  }

  .action-btn {
    width: 100%;
  }

  .specs-grid {
    grid-template-columns: 1fr;
  }

  .related-grid {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 20px;
  }

  .product-details-wrapper {
    padding: 0 15px;
  }

  .product-header-content {
    padding: 0 15px;
  }

  .breadcrumb-content {
    padding: 0 15px;
  }
}

@media (max-width: 480px) {
  .product-header {
    padding: 20px 0;
  }

  .product-header h1 {
    font-size: 1.5rem;
  }

  .product-title {
    font-size: 1.5rem;
  }

  .product-price {
    font-size: 1.3rem;
  }

  .action-buttons {
    gap: 10px;
  }

  .action-btn {
    padding: 12px 20px;
    font-size: 14px;
  }
}
